html {
    height:100%;
    background-color: #121e2d;
    font-family: 'Roboto', 'Arial', sans-serif;
    overflow-x: hidden;
    scroll-behavior: smooth;
}

body, #root {
    overflow-x: hidden;
    position: relative;
}

* {
    box-sizing: border-box;
}

